
'use client';

import React, { createContext, useContext, useState, ReactNode, useMemo, useEffect } from 'react';
import type { Employee } from '@/lib/types';
import { useUser as useFirebaseAuthUser } from '@/firebase';
import { doc } from 'firebase/firestore';
import { useFirestore, useDoc } from '@/firebase';

type UserContextType = {
  user: (Employee & { uid: string }) | null;
  loading: boolean;
};

const UserContext = createContext<UserContextType | undefined>(undefined);

export function UserProvider({ children }: { children: ReactNode }) {
  const { user: firebaseUser, loading: authLoading } = useFirebaseAuthUser();
  const firestore = useFirestore();
  
  const userDocRef = useMemo(() => {
    if (!firestore || !firebaseUser) return null;
    return doc(firestore, 'users', firebaseUser.uid);
  }, [firestore, firebaseUser]);

  const { data: employeeData, loading: userLoading } = useDoc(userDocRef);

  const [user, setUser] = useState<(Employee & { uid: string }) | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    setLoading(authLoading || userLoading);
    if (!authLoading && !userLoading) {
      if (firebaseUser && employeeData) {
        setUser({
          ...(employeeData as Employee),
          uid: firebaseUser.uid,
        });
      } else {
        setUser(null);
      }
    }
  }, [firebaseUser, employeeData, authLoading, userLoading]);

  const contextValue = useMemo(() => ({
    user,
    loading
  }), [user, loading]);

  return (
    <UserContext.Provider value={contextValue}>
      {children}
    </UserContext.Provider>
  );
}

export function useUser() {
  const context = useContext(UserContext);
  if (context === undefined) {
    throw new Error('useUser must be used within a UserProvider');
  }
  return context;
}
