
'use client';

import { useEffect, useState, useRef } from 'react';
import {
  onSnapshot,
  query,
  collection,
  where,
  orderBy,
  limit,
  startAfter,
  endBefore,
  limitToLast,
  type DocumentData,
  type Query,
  type FirestoreError,
} from 'firebase/firestore';

export interface UseCollectionOptions<T> {
  query?: [string, '==', any] | [string, '!=', any] | [string, '>', any] | [string, '<', any] | [string, '>=', any] | [string, '<=', any] | [string, 'array-contains', any] | [string, 'in', any[]] | [string, 'array-contains-any', any[]];
  orderBy?: [string, 'asc' | 'desc'];
  limit?: number;
  startAfter?: any;
  endBefore?: any;
  limitToLast?: number;
}

export function useCollection<T>(
  ref: Query<T | DocumentData> | null,
  options?: UseCollectionOptions<T>
) {
  const [data, setData] = useState<T[] | null>(null);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<FirestoreError | null>(null);

  const optionsRef = useRef(options);

  useEffect(() => {
    if (!ref) {
      setLoading(false);
      return;
    }

    let q = ref;

    if (optionsRef.current?.query) {
      q = query(q, where(...optionsRef.current.query));
    }
    if (optionsRef.current?.orderBy) {
      q = query(q, orderBy(...optionsRef.current.orderBy));
    }
    if (optionsRef.current?.startAfter) {
      q = query(q, startAfter(optionsRef.current.startAfter));
    }
    if (optionsRef.current?.endBefore) {
        q = query(q, endBefore(optionsRef.current.endBefore));
    }
    if (optionsRef.current?.limit) {
      q = query(q, limit(optionsRef.current.limit));
    }
     if (optionsRef.current?.limitToLast) {
      q = query(q, limitToLast(optionsRef.current.limitToLast));
    }

    const unsubscribe = onSnapshot(
      q,
      (snapshot) => {
        const result = snapshot.docs.map((doc) => ({ id: doc.id, ...doc.data() } as T));
        setData(result);
        setLoading(false);
      },
      (err) => {
        setError(err);
        setLoading(false);
      }
    );

    return () => unsubscribe();
  }, [ref]);

  return { data, loading, error };
}
